import React from 'react'
import Scrolling from 'react-horizontal-scrolling-menu'
import Card from './categoryCards.js'
export default class Category extends React.Component{
	constructor(props){
		super(props)
		this.list=[
		{text:"Today's Current",style:'1'},
		{text:'National',style:'2'},
		{text:'Economy',style:'3'},
		{text:'Envirnoment',style:'4'},
		{text:'Political',style:'1'},
		{text:'Science&Tech',style:'2'},
		{text:'Budget',style:'3'},
		{text:'EconomicSurvey',style:'4'},
		{text:'Environment',style:'1'},
		{text:'International',style:'2'},
		{text:'Art',style:'3'},
		{text:'Report"s',style:'4'},
		{text:'International Bodies',style:'1'},
		{text:'India Year Book',style:'2'},
		{text:'Schemes',style:'3'},
		{text:'Geography',style:'4'},
		{text:'Economic Survey',style:'1'},
		{text:'Download',style:'2'}
		]
		this.clicked = this.clicked.bind(this)
	}
	clicked(value){
		this.props.category(value)
	}
	render(){
		return(
			<div className="category mr-2 p-3 mt-5 mb-3">
				<Scrolling
				data={this.list.map((data,key)=>
					<Card style={data.style} text={data.text} key={key} onClick={()=>this.clicked(data.text)}/>
				)}
				alignCenter={false}
				scrollBy={1}
				hideArrows={true}
				/>
			</div>
			)
	}
}