import React from 'react'
import Header from './header'
import Scroll from './scrollTheme.js'
import BlogCard from './BlogCard.js'
import data from '../moc-db1.json'
import Slider from "react-slick"
import "slick-carousel/slick/slick.css"
import "slick-carousel/slick/slick-theme.css"
import Modal from 'react-responsive-modal'

export default class Current extends React.Component{
	constructor(props){
		super(props)
		this.state={
			loading:false,
			latestList:data,
			open:false,
			popUpValue:[],
			showCard:false
		}
		this.openPopUp = this.openPopUp.bind(this)
		this.closeDialog= this.closeDialog.bind(this)
		this.toggle = this.toggle.bind(this)
	}
	toggle(){
		this.setState({
			showCard:!this.state.showCard
		})
	}
	openPopUp(value){
		console.log('inside home with value',value)
		this.setState({
			open:true,
			popUpValue:value
		})
	}
	closeDialog(){
		this.setState({
			open:false,
			popUpValue:[]
		})
	}
	render(){
			const settings = {
      dots: false,
      arrows: true,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,

    };
		return(
			<div>
				
				<div className="today-current mt-4 mb-4 mr-2 ml-2 pb-5 pr-4 pl-4 pt-2">
					<div className="mt-3 pl-1 row justify-content-center">
					<div className="col-md-2 col-lg-2 col-sm-2 "><h4 className="d-inline head mr-2">Budget</h4><span className="tog" onClick={this.toggle}><i class="arrow1 down-arrow"></i></span></div> 
					<div className="col-sm-10 col-md-10 col-lg-10">	 
						
					</div>
					</div>
					{this.state.showCard ? 
					
						<Slider {...settings} > 
						
               		{this.state.latestList.map((e, i) =>
                      (	
                      <BlogCard
                      showExpanded={this.openPopUp}
                        key={i}
                        title={e.title}
                        subtitle={e.subTitle}
                        cont={e.content}
                        views={e.views}
                        lang={e.lang}
                        author={e.author}
                        authorImg={e.authorImg}
                        thumbnail={e.thumbnail}
                        type={e.type}
                        metatags={e.metaTag}
                        tags={e.tag}
                        _id={e._id}
                      />
                    )
                  )
            }
            </Slider>
            
            :
            <span></span>}
				</div>
			<Modal open={this.state.open} onClose={this.closeDialog} center>
				 <div className="mr-2 ml-2">
				 	<h2 className="mt-4 mb-2">{this.state.popUpValue.title}</h2>
				 	<img src={this.state.popUpValue.thumbnail} className="img-modal img-fluid img-rounded"/>
				 	<div >
				 		<span  >MetaTags: {this.state.popUpValue.metatags}</span>
				 		<span className="span-modal"><i className="fas fa-eye fa-lg"></i>&nbsp;&nbsp;: {this.state.popUpValue.views}</span>
				 	</div>
				 	<div>
				 		<span>Tags: {this.state.popUpValue.tags}</span>
				 		<h4 className="mt-2">Author: {this.state.popUpValue.author}</h4>
				 	</div>
				 	<div>
				 		{this.state.popUpValue.subtitle}
				 		<p>{this.state.popUpValue.cont}</p>
				 	</div>
				 </div>
				 </Modal>
			</div>
			)
	}
}